import React, { useState } from "react";
import { Page, Card, DisplayText, Layout, Stack } from "@shopify/polaris";
import Switch from "react-switch";
import "./settings.css";

const Structer = ({ Title, Component }) => {
  return (
    <Layout>
      <Layout.Section oneHalf>
        <DisplayText size="extraSmall">{Title}</DisplayText>
      </Layout.Section>
      <Layout.Section oneHalf>{Component}</Layout.Section>
    </Layout>
  );
};
const Settings = () => {
  const Title = <DisplayText size="small">Settings</DisplayText>;
  const [checked, setchecked] = useState(false);
  const ThemeChange = () => {
    return <Switch onChange={(data) => setchecked(data)} checked={checked} />;
  };
  return (
    <Page fullWidth>
      <Card title={Title}>
        <div className="settings">
          <Stack vertical="false">
            <Structer Title={"Theme"} Component={<ThemeChange />} />
          </Stack>
        </div>
      </Card>
    </Page>
  );
};

export default Settings;
