import React, { useState, useCallback, useEffect } from "react";
import {
  Layout,
  Stack,
  TextField,
  DisplayText,
  FormLayout,
  ChoiceList,
  Button,
} from "@shopify/polaris";
import DropDown from "./citydropdown";
import Services from "./services";
import { API } from "../../API";
import axios from "axios";
import Switch from "react-switch";

const FormStucher = (Label, injectablecomponent) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{injectablecomponent}</Layout.Section>
    </Layout>
  );
};

