import React, { useCallback, useState } from "react";
import {
  Button,
  TextField,
  Page,
  DisplayText,
  Card,
  Modal,
  Layout,
  DatePicker,
  Stack,
  Banner,
  TextContainer,
} from "@shopify/polaris";
import moment from "moment";
import axios from "axios";
import ReportTable from "./ReportTable";
import Headerselect from "./SelectionDropdown";
import "./App.css";
import { API } from "../API";

const Reports = () => {
  const [activecomplete, setActivecomplete] = useState(false);

  const handleActivebanner = useCallback(
    () => setActivecomplete(!activecomplete),
    [activecomplete]
  );

  const handlecompleteChange = () => {
    handleActivebanner();
  };
  const [activeemail, setActiveemail] = useState(false);
  const handleactiveemail = useCallback(() => setActiveemail(!activeemail), [
    activeemail,
  ]);

  const monthIndex = moment().month().toString();
  const year = moment().year().toString();
  const [activepick, setActivepick] = useState(false);
  const handleChangepick = useCallback(() => setActivepick(!activepick), [
    activepick,
  ]);
  const [activedrop, setActivedrop] = useState(false);
  const handleChangedrop = useCallback(() => setActivedrop(!activedrop), [
    activedrop,
  ]);

  /* DATEPICKER PICK STATE START HERE */
  const [{ pickmonth, pickyear }, setPickDate] = useState({
    pickmonth: parseInt(monthIndex),
    pickyear: parseInt(year),
  });
  const [selectedpickDates, setSelectedpickDates] = useState({
    start: new Date(moment().toString()),
    end: new Date(moment().toString()),
  });

  const handlePickMonthChange = useCallback(
    (pickmonth, pickyear) => setPickDate({ pickmonth, pickyear }),
    []
  );

  const [pickdatevalue, setPickdatevalue] = useState(
    moment().format("MMMM Do YYYY").toString()
  );

  const Datepickchanger = (Date) => {
    /* THIS FUNCATION WILL CHANGE THE DATE IN STATE AND CLOSE THE MODEL */
    setSelectedpickDates(Date);
    setPickdatevalue(moment(Date.start).format("MMMM Do YYYY").toString());
    handleChangepick();
  };
  /* END HERE */

  /* DATEPICKER DROP STATE START HERE */
  const [{ dropmonth, dropyear }, setdropDate] = useState({
    dropmonth: parseInt(monthIndex),
    dropyear: parseInt(year),
  });
  const [selecteddropDates, setSelecteddropDates] = useState({
    start: new Date(moment().add(1, "d").toString()),
    end: new Date(moment().add(1, "d").toString()),
  });

  const handleDropMonthChange = useCallback(
    (dropmonth, dropyear) => setdropDate({ dropmonth, dropyear }),
    []
  );

  const [dropdatevalue, setDropdatevalue] = useState(
    moment().add(1, "d").format("MMMM Do YYYY").toString()
  );
  const Datedropchanger = (Date) => {
    /* THIS FUNCATION WILL CHANGE THE DATE IN STATE AND CLOSE THE MODEL */
    setSelecteddropDates(Date);
    setDropdatevalue(moment(Date.start).format("MMMM Do YYYY").toString());
    handleChangedrop();
  };
  /* END HERE */
  const [headerx, setHeaderx] = useState([]);
  const [ChoosedStore, setChoosedStore] = useState("Na");
  const HandleClick = () => {
    const StorewisePostdata = {
      fromx: selectedpickDates.start,
      tox: selecteddropDates.start,
      headerx: headerx,
    };
    if (localStorage.getItem("role") === "Admin") {
      if (ChoosedStore === "Na") {
        axios
          .post(API + "/report/exportdatabase", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            console.log(Response.data);
            handlecompleteChange();
            document.location.href =
              API + "/report/download/" + Response.data["filename"];
          });
      } else {
        let StorewisePostdata = {
          fromx: selectedpickDates.start,
          tox: selecteddropDates.start,
          headerx: headerx,
          storeid: ChoosedStore,
        };
        axios
          .post(API + "/report/store", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            console.log(Response.data);
            handlecompleteChange();
            document.location.href =
              API + "/report/download/" + Response.data["filename"];
          });
      }
    } else {
      axios
        .post(API + "/report", StorewisePostdata, {
          headers: {
            "content-type": "text/json",
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((Response) => {
          console.log(Response.data);
          handlecompleteChange();
          document.location.href =
            API + "/report/download/" + Response.data["filename"];
        });
    }
  };

  const HandleClickpdf = () => {
    console.log(headerx)
    const StorewisePostdata = {
      fromx: selectedpickDates.start,
      tox: selecteddropDates.start,
      headerx: headerx,
    };
    if (localStorage.getItem("role") === "Admin") {
      if (ChoosedStore === "Na") {
        axios
          .post(API + "/report/pdf/exportdatabase", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            console.log(Response.data);
            handlecompleteChange();
            document.location.href =
              API + "/report/download/" + Response.data["filename"];
          });
      } else {
        let StorewisePostdata = {
          fromx: selectedpickDates.start,
          tox: selecteddropDates.start,
          headerx: headerx,
          storeid: ChoosedStore,
        };
        axios
          .post(API + "/report/pdf/store", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            console.log(Response.data);
            handlecompleteChange();
            document.location.href =
              API + "/report/download/" + Response.data["filename"];
          });
      }
    } else {
      axios
        .post(API + "/report/pdf", StorewisePostdata, {
          headers: {
            "content-type": "text/json",
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((Response) => {
          console.log(Response.data);
          handlecompleteChange();
          document.location.href =
            API + "/report/download/" + Response.data["filename"];
        });
    }
  };

  const [email, setemail] = useState("");

  const handleChangeemail = useCallback((newValue) => setemail(newValue), []);

  const [emailsuc, setemailsuc] = useState(false);

  const handleChangsuccess = useCallback(() => setemailsuc(!emailsuc), [
    emailsuc,
  ]);

  const [emailfailed, setemailfaile] = useState(false);

  const handleChangfailed = useCallback(() => setemailfaile(!emailfailed), [
    emailfailed,
  ]);

  const [failedText, setfailedText] = useState("");
  const [sucessText, setsucessText] = useState("");

  const HandleClickemail = () => {
    const StorewisePostdata = {
      fromx: selectedpickDates.start,
      tox: selecteddropDates.start,
      headerx: headerx,
      email: email,
    };
    if (localStorage.getItem("role") === "Admin") {
      if (ChoosedStore === "Na") {
        axios
          .post(API + "/report/email/exportdatabase", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            setsucessText(Response.data.filename);
            handleChangsuccess();
          })
          .catch((Response) => {
            setfailedText("Email Send Faild");
            handleChangfailed();
          });
      } else {
        let StorewisePostdata = {
          fromx: selectedpickDates.start,
          tox: selecteddropDates.start,
          headerx: headerx,
          email: email,
          storeid: ChoosedStore,
        };
        axios
          .post(API + "/report/email/store", StorewisePostdata, {
            headers: {
              "content-type": "text/json",
              Authorization: "Bearer " + localStorage.getItem("token"),
            },
          })
          .then((Response) => {
            setsucessText(Response.data.filename);
            handleChangsuccess();
          })
          .catch((Response) => {
            setfailedText("Email Send Faild");
            handleChangfailed();
          });
      }
    } else {
      axios
        .post(API + "/report/email", StorewisePostdata, {
          headers: {
            "content-type": "text/json",
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((Response) => {
          setsucessText(Response.data.filename);
          handleChangsuccess();
        })
        .catch((Response) => {
          setfailedText("Email Send Faild");
          handleChangfailed();
        });
    }
  };

  const Title = () => {
    return (
      <Stack>
        <Stack.Item fill>
          <DisplayText size="small" className="titleText">
            Datewise Report
          </DisplayText>
        </Stack.Item>
      </Stack>
    );
  };

  return (
    <Page fullWidth>
      <Card title={<Title />} sectioned>
        <Layout>
          <Layout.Section oneHalf>
            <DisplayText size="extrasmall">From : </DisplayText>
            <TextField
              type="text"
              value={pickdatevalue}
              onFocus={() => {
                handleChangepick();
              }}
            />

            <Modal open={activepick} onClose={handleChangepick} title="From">
              <Modal.Section>
                <DatePicker
                  month={pickmonth}
                  year={pickyear}
                  onChange={Datepickchanger}
                  onMonthChange={handlePickMonthChange}
                  selected={selectedpickDates}
                />{" "}
              </Modal.Section>
            </Modal>
          </Layout.Section>
          <Layout.Section oneHalf>
            <DisplayText size="extrasmall">To : </DisplayText>
            <TextField
              type="text"
              value={dropdatevalue}
              onFocus={() => {
                handleChangedrop();
              }}
            />
            <Modal open={activedrop} onClose={handleChangedrop} title="To">
              <Modal.Section>
                <DatePicker
                  month={dropmonth}
                  year={dropyear}
                  onChange={Datedropchanger}
                  onMonthChange={handleDropMonthChange}
                  selected={selecteddropDates}
                />
              </Modal.Section>
            </Modal>
          </Layout.Section>
          <Layout.Section>
            <Headerselect setheader={setHeaderx} />
          </Layout.Section>
          <Layout.Section>
            <Button
              primary
              size="slim"
              onClick={HandleClick}
              connectedDisclosure={{
                accessibilityLabel: "Other save actions",
                actions: [
                  { content: "Export as PDF", onAction: HandleClickpdf },
                  { content: "Share in Email", onAction: handleactiveemail },
                ],
              }}
            >
              Export
            </Button>
          </Layout.Section>
        </Layout>
      </Card>

      <ReportTable setChoosedStore={setChoosedStore} />
      <Modal open={activecomplete} onClose={handlecompleteChange}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>Downloading report starting...</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>

      <Modal open={activeemail} onClose={handleactiveemail}>
        <Modal.Section>
          <Stack vertical="false">
            <Banner>Enter your email below to send report in email</Banner>
            <TextField
              type="email"
              placeholder={"anon42237@gmail.com"}
              value={email}
              onChange={handleChangeemail}
              connectedRight={
                <Button primary onClick={HandleClickemail}>
                  Send
                </Button>
              }
            />

            <Modal open={emailsuc} onClose={handleChangsuccess}>
              <Modal.Section>
                <TextContainer>
                  <Banner status="success">
                    <p>{sucessText}</p>
                  </Banner>
                </TextContainer>
              </Modal.Section>
            </Modal>

            <Modal open={emailfailed} onClose={handleChangfailed}>
              <Modal.Section>
                <TextContainer>
                  <Banner status="critical">
                    <p>{failedText}</p>
                  </Banner>
                </TextContainer>
              </Modal.Section>
            </Modal>
          </Stack>
        </Modal.Section>
      </Modal>
    </Page>
  );
};

export default Reports;
