import React, { useState, useEffect } from "react";
import {
  Layout,
  Stack,
  FormLayout,
  TextField,
  DisplayText,
  Button
} from "@shopify/polaris";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import Select from "react-select";
import { API } from "../../API";
import axios from "axios";
import Switch from "react-switch"

const FormTextStruc = (Label, injectcomponent) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{injectcomponent}</Layout.Section>
    </Layout>
  );
};

const FormStruc = (Label, Field) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{Field}</Layout.Section>
    </Layout>
  );
};

const Usersetting = ({ id, setActivexz, handleerror, handleChangesucc }) => {
  const [Username, setUsername] = useState("");
  const [Phone, setPhone] = useState(0);
  const [checked, setchecked] = useState(true)

  const options = ["PickupBoy", "StoreManager", "CoustmerCare", "Admin"];

  const [Role, setRole] = useState("PickupBoy");

  const checkrole = (data) => {
    if (Role === options[0]) {
      return FormStruc("City", <DropDownRegion data={data} />);
    } else if (Role === options[1]) {
      return FormStruc("City", <DropDownRegion data={data} />);
    } else {
      return "";
    }
  };

  const checkroleforarea = (data) => {
    if (Role === options[0]) {
      return FormStruc("Store", <DropDownArea Defaultstore={data} />);
    } else if (Role === options[1]) {
      return FormStruc("Store", <DropDownArea Defaultstore={data} />);
    } else {
      return "";
    }
  };

  const DropDown = (Defaultvalue) => {
    return (
      <Dropdown
        options={options}
        value={Defaultvalue.Defaultvalue}
        onChange={(data) => {
          setRole(data["value"]);
        }}
        placeholder="Select Role"
      />
    );
  };

  const [optionsRegion, setOptionsRegion] = useState(["Guwahati"]);
  const [SelectedRegion, setSelectedRegion] = useState({ value: "Guwahati" });
  const Getregion = () => {
    axios
      .get(API + "/regions", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        setOptionsRegion(data.data["data"]);
        console.log(data.data["data"]);
      });
  };

  const Getarea = (Region) => {
    axios
      .get(API + "/regions/area?area=" + Region.value, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        setArea(data.data["data"]);
      })
      .catch((data) => {
        console.log(data);
      });
  };

  useEffect(() => {
    Getregion();
    Getarea(SelectedRegion);
  }, []);

  const DropDownRegion = (data) => {
    console.log(data.data);
    return (
      <Select
        options={optionsRegion}
        onChange={(data) => {
          setSelectedRegion(data);
          Getarea(data);
        }}
        defaultValue={data.data}
        placeholder="Select City"
      />
    );
  };

  const [Area, setArea] = useState(["Please select Region First"]);
  const [SelectArea, setSelectArea] = useState({ value: "empty" });
  const DropDownArea = (Defaultstore) => {
    return (
      <Select
        options={Area}
        onChange={(data) => {
          setSelectArea(data);
        }}
        openOnFocus={true}
        autofocus={true}
        placeholder="Select Store"
        defaultValue={Defaultstore.Defaultstore}
      />
    );
  };

  const [value, setValue] = useState("");

  const address = (
    <TextField
      value={value}
      onChange={(data) => {
        setValue(data);
      }}
      multiline={2}
    />
  );

  const clean = () => {
    setUsername("");
    setPhone("");
    setValue("");
    setSelectedRegion({ value: "empty" });
    setSelectedRegion({ value: "empty" });
  };
  const CreateUserX = () => {
    let statuschecked = "active"
    if (Role === "CoustmerCare" || Role === "Admin") {
      setSelectArea({ value: "empty" });
      setSelectedRegion({ value: "empty" });
    }
    if (checked === true) {
      statuschecked = "active"
    }
    else {
      statuschecked = "deactive"
    }
    const postdata = {
      username: Username,
      phone: Phone,
      address: value,
      role: Role,
      region: SelectedRegion.value,
      store: SelectArea.value,
      status: statuschecked
    };
    console.log(postdata);
    axios
      .post(API + "/users/update", postdata, {
        headers: {
          "content-type": "text/json",
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setActivexz(false);
        handleChangesucc();
        clean();
      })
      .catch((response) => {
        setActivexz(false);
        handleerror();
      });
  };

  const Fetchuserinfo = () => {
    axios
      .get(API + "/users/" + id, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        const data = response.data.data[0];
        setUsername(data.username);
        setPhone(data.phone);
        setValue(data.address);
        setRole(data.role);
        setSelectedRegion({ value: data.region, label: data.region });
        setSelectArea({ value: data.area, label: data.area });
        if (data.status === "active"){
          setchecked(true)
        }
        else {
          setchecked(false)
        }
      });
  };

  useEffect(() => {
    Fetchuserinfo();
  }, []);
  return (
    <div>
      <FormLayout>
        <Stack vertical="true" distribution="fill" spacing="extraTight">
          {FormTextStruc(
            "Name",
            <TextField
              type="text"
              placeholder="script"
              value={Username}
              onChange={(data) => {
                setUsername(data);
              }}
              disabled
            />
          )}
          {FormTextStruc(
            "Phone",
            <TextField
              type="text"
              placeholder="9365708946"
              value={Phone}
              onChange={(data) => {
                setPhone(data);
              }}
            />
          )}
          {FormStruc("Address", address)}
          {FormStruc("Role", <DropDown Defaultvalue={Role} />)}
          {checkrole(SelectedRegion)}
          {checkroleforarea(SelectArea)}
          {FormStruc(
            "User Status",
            <Switch onChange={(data) => setchecked(data)} checked={checked} />
          )}
          <Button
            primary
            size="slim"
            onClick={() => {
              CreateUserX();
            }}
          >
            Update User
          </Button>
        </Stack>
      </FormLayout>
    </div>
  );
};

export default Usersetting;
