import React, { useState, useEffect } from "react";
import Banner from "../../Components/Banner/Banner";
import BannerView from "../Card/BannerView";
import axios from "axios";
import {
  Page,
  Layout,
  Stack,
  DisplayText,
} from "@shopify/polaris";
import { API } from "../API";
import "./App.css";
import SelectStore from "./SelectStore";

const Combiner = () => {
  const [pendingcarry, setpendingcarry] = useState(0);
  const [todaycarry, settodaycarry] = useState(0);
  const [todaysrev, settodaysrev] = useState(0);
  useEffect(() => {
    GetCarryData();
  }, []);

  const GetCarryData = () => {
    axios
      .get(API + "/overview/todaycarry", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((res) => {
        const todaycarry = res.data;
        settodaycarry(todaycarry.todaycarry);
        setpendingcarry(todaycarry.pendingcarry);
        settodaysrev(todaycarry.todayrevenue);
        console.log(todaycarry);
      });
  };

  const GetCarryDataStoreBased = (storeid) => {
    axios
      .get(API + "/overview/todaycarry/" + storeid, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((res) => {
        const todaycarry = res.data;
        settodaycarry(todaycarry.todaycarry);
        setpendingcarry(todaycarry.pendingcarry);
        settodaysrev(todaycarry.todayrevenue);
        console.log(todaycarry);
      });
  };

  const formatNumber = (num) => {
    var n1, n2;
    num = num + "" || "";
    n1 = num.split(".");
    n2 = n1[1] || null;
    n1 = n1[0].replace(/(\d)(?=(\d\d)+\d$)/g, "$1,");
    num = n2 ? n1 + "." + n2 : n1;
    return num;
  };

  const Naruto = "Date  : " + new Date().toLocaleDateString();
  const Newnaruto = (
    <Stack vertical="false">
      <h1 className="Heading">Overview</h1>
      <Stack alignment="center">
        <Stack.Item fill>
          <div className="Date">
            <DisplayText size="small">{Naruto}</DisplayText>
          </div>
        </Stack.Item>
        <Stack.Item>
          <div className="Selecttion">
            <SelectStore fetchingfunc={GetCarryDataStoreBased} />
          </div>
        </Stack.Item>
      </Stack>
    </Stack>
  );
  return (
    <Page fullWidth title={Newnaruto}>
      <Layout>
        <Layout.Section oneHalf>
          <Stack distribution="fillEvenly">
            <Banner Num={formatNumber(pendingcarry.pendingpickup)} Text="Pending Pickup" />
            <Banner Num={formatNumber(pendingcarry.pendingdelivery)} Text="Pending Delivery" />
          </Stack>
        </Layout.Section>

        <Layout.Section oneHalf>
          <Stack distribution="fillEvenly">
            <Banner Num={formatNumber(todaycarry.delivery)} Text="Today's Delivery" />
            <Banner Num={formatNumber(todaycarry.pickup)} Text="Today's Pickups" />
          </Stack>
        </Layout.Section>
        <Layout.Section oneHalf>
          <Stack distribution="fillEvenly">
            <BannerView
              TottalDelivery={formatNumber(
                todaysrev.Todayestimatedeevenue
              )}
              title={
                <DisplayText size="small">
                  Today's Estimated Revenue
                </DisplayText>
              }
            />
            <BannerView
              TottalDelivery={formatNumber(todaysrev.Todaycollectedamount)}
              title="Today's Collection"
            />
          </Stack>
        </Layout.Section>
      </Layout>
    </Page>
  );
};

export default Combiner;
