import React, { useState, useCallback } from "react";
import {
  Page,
  Layout,
  Card,
  Stack,
  TextField,
  DisplayText,
  FormLayout,
  Modal,
  Banner,
  TextContainer,
  Button,
} from "@shopify/polaris";
import DropDown from "./servicecatagorydropdown";
import Select from "react-select";
import "./Createservice.css";
import Temswriter from "./termswriter";
import Addons from "./AddonDropdown";
import Switch from "react-switch";
import { API } from "../../API";
import axios from "axios";
import RichTextEditor from "react-rte";

const FormStucher = (Label, injectablecomponent) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{injectablecomponent}</Layout.Section>
    </Layout>
  );
};

const UnitandPrice = ({ Price, setPricex, options, Unit, setUnit }) => {
  return (
    <TextField
      placeholder="Enter Price"
      prefix=""
      type="number"
      value={Price}
      onChange={setPricex}
      connectedRight={
        <Select
          options={options}
          value={Unit}
          onChange={(data) => {
            setUnit(data);
          }}
          openOnFocus={true}
          autofocus={true}
          menuPortalTarget={document.body}
          styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
          className="Unit"
        />
      }
    ></TextField>
  );
};

const CreateService = () => {
  const options = [
    { value: "KG", label: "KG" },
    { value: "per-piece", label: "per-piece" },
  ];
  const [name, setName] = useState("");
  const [Price, setPrice] = useState();
  const handleprincing = useCallback((newValue) => setPrice(newValue), []);
  const [Unit, setUnit] = useState(options[0]);

  const [SelectedCategory, setSelectedCategory] = useState([
    {
      value: "please select a service category",
      label: "please select a service category",
    },
  ]);

  const [MinimumPrice, setMinimumPrice] = useState(100);
  const [ProccessingTime, setProccessingTime] = useState(24);
  const [Discription, setDiscription] = useState("");
  const [checked, setchecked] = useState(false);
  const [inputList, setInputList] = useState([
    { Addonsname: "", price: "", unit: "" },
  ]);

  const [value, setvalue] = useState(RichTextEditor.createEmptyValue());
  // Temps and condition still not added
  const Createservicesenddata = () => {
    console.log(inputList);
    const postdata = {
      name: name,
      category: SelectedCategory.value,
      price: Price,
      unit: Unit.value,
      minimumprice: MinimumPrice,
      ptime: ProccessingTime,
      description: Discription,
      deliveryavbl: checked,
    };
    console.log(postdata);
    axios
      .post(API + "/services", postdata, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((reponsive) => {
        if (reponsive.data.services === "New service created successfully") {
          setText(reponsive.data.services);
          setActive(true);
        }else {
          setText(reponsive.data.services);
          settriggererror(true);
        }

        axios
          .post(
            API + "/addons/",
            {
              addons: inputList,
              servicename: name,
            },
            {
              headers: {
                Authorization: "Bearer " + localStorage.getItem("token"),
              },
            }
          )
          .then((response) => {
            console.log(response.data);
          })
          .catch((response) => {
            setText("There was an Error Addons cant be added");
            settriggererror(true);
          });
      })
      .catch((response) => {
        setText("There was an Error service cant be added");
        settriggererror(true);
      });
  };
  const [Text, setText] = useState("");

  const [active, setActive] = useState(false);
  const [triggererror, settriggererror] = useState(false);

  const handleChange = useCallback(() => setActive(!active), [active]);

  const handleerror = useCallback(() => settriggererror(!triggererror), [
    triggererror,
  ]);
  return (
    <Page fullWidth>
      <Card title="Create Service">
        <FormLayout>
          <div className="addpaddingforneworder">
            <Stack vertical="true" distribution="fill" spacing="extraTight">
              {FormStucher(
                "Service Name*",
                <TextField
                  placeholder="Service Name"
                  value={name}
                  onChange={(data) => {
                    setName(data);
                  }}
                ></TextField>
              )}
              {FormStucher(
                "Service Category*",
                <DropDown
                  SelectedCategory={SelectedCategory}
                  setSelectedCategory={setSelectedCategory}
                />
              )}
              {FormStucher(
                "Price & Unit* ",
                <UnitandPrice
                  Price={Price}
                  setPricex={handleprincing}
                  options={options}
                  Unit={Unit}
                  setUnit={setUnit}
                />
              )}
              {FormStucher(
                "Minimum Price* ",
                <TextField
                  prefix=""
                  type="number"
                  placeholder="Enter minimum price"
                  value={MinimumPrice}
                  onChange={(price) => {
                    setMinimumPrice(price);
                  }}
                  connectedRight={<Button>{Unit.value}</Button>}
                ></TextField>
              )}
              {FormStucher(
                "Processing Time* ",
                <TextField
                  placeholder="Enter time"
                  type="number"
                  value={ProccessingTime}
                  onChange={(data) => {
                    setProccessingTime(data);
                  }}
                  connectedRight={<Button>{"Hrs"}</Button>}
                ></TextField>
              )}
              {FormStucher(
                "Description* ",
                <TextField
                  multiline={2}
                  value={Discription}
                  onChange={(data) => {
                    setDiscription(data);
                  }}
                  placeholder="Write a Short description what this service do!!!"
                ></TextField>
              )}
              {FormStucher(
                "Pickup/Drop Available* ",
                <Switch
                  onChange={(data) => setchecked(data)}
                  checked={checked}
                />
              )}
              {FormStucher(
                "Add Ons with Price ",
                <Addons inputList={inputList} setInputList={setInputList} />
              )}
              {FormStucher(
                "Terms and Conditions ",
                <Temswriter setvalue={setvalue} />
              )}
            </Stack>
            <Button
              primary
              onClick={() => {
                Createservicesenddata();
              }}
            >
              Submit
            </Button>
          </div>
        </FormLayout>
      </Card>
      <Modal open={active} onClose={handleChange}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>{Text}</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
      <Modal open={triggererror} onClose={handleerror}>
        <Modal.Section>
          <TextContainer>
            <Banner status="critical">
              <p>{Text}</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
    </Page>
  );
};

export default CreateService;
