const FormStucher = (Label, injectablecomponent) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{injectablecomponent}</Layout.Section>
    </Layout>
  );
};

