import React, { useState, useEffect, useCallback } from "react";
import Select from "react-select";
import axios from "axios";
import {
  Stack,
  Button,
  Modal,
  TextField,
} from "@shopify/polaris";
import Bannerx from "./Banner";
import { API } from "../../API";

const MyComponent = ({ City,setCity }) => {
  const [options, setoptions] = useState([""]);
  const FetchOption = () => {
    axios
      .get(API + "/regions", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setoptions(response.data.data);
      });
  };
  useEffect(() => {
    FetchOption();
  }, []);
  const [categoryvalue, setCategoryvalue] = useState("");
  const addcategory = () => {
    console.log(categoryvalue)
    axios
      .post(
        API + "/regions",
        {
          city: categoryvalue,
        },
        {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      )
      .then((response) => {
        let responsex = response.data.data;
        if (responsex === "success") {
          Bannersuccesshandler(true);
          setCategoryvalue("");
          handleChange();
          FetchOption();
        } else {
          Bannererrorhandler(true);
          setCategoryvalue("");
          handleChange();
        }
      })
      .catch(() => {
        Bannererrorhandler(true);
        setCategoryvalue("");
        handleChange();
      });
  };

  const [Bannersuccessactive, setBannersuccessactive] = useState(false);
  const Bannersuccesshandler = useCallback(
    () => setBannersuccessactive(!Bannersuccessactive),
    [Bannersuccessactive]
  );

  const [Bannererroractive, setBannererroractive] = useState(false);
  const Bannererrorhandler = useCallback(
    () => setBannererroractive(!Bannererroractive),
    [Bannererroractive]
  );

  const [active, setActive] = useState(false);

  const handleChange = useCallback(() => setActive(!active), [active]);

  const activator = (
    <Button primary  onClick={handleChange}>
      Add City
    </Button>
  );
  return (
    <div>
      <Stack distribution="fillEvenly">
        <Select
          options={options}
          value={City}
          onChange={(data) => {
            setCity(data);
          }}
          placeholder="please select a city"
          openOnFocus={true}
          autofocus={true}
          menuPortalTarget={document.body}
          styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
        />
        {activator}
      </Stack>
      <Modal open={active} onClose={handleChange} title="Add city">
        <Modal.Section>
          <TextField
            placeholder="ex: Guwahati"
            label="city Name"
            value={categoryvalue}
            onChange={(data) => {
              setCategoryvalue(data);
            }}
            connectedRight={
              <Button
                primary
                onClick={() => {
                  addcategory();
                }}
              >
                Add City{" "}
              </Button>
            }
          ></TextField>
        </Modal.Section>
      </Modal>
      <Bannerx
        active={Bannersuccessactive}
        handleChange={Bannersuccesshandler}
        Status={"success"}
        Msg={"New city has been successfully added!!!"}
      />
      <Bannerx
        active={Bannererroractive}
        handleChange={Bannererrorhandler}
        Status={"critical"}
        Msg={"New city can't be added there is a some kind of error!!!"}
      />
    </div>
  );
};

export default MyComponent;
