import React, { useCallback, useState, useRef } from "react";
import {
  Card,
  ActionList,
  Frame,
  Navigation,
  TopBar,
  Banner,
} from "@shopify/polaris";
import {
  HomeMajor,
  OnlineStoreMajor,
  ReportMinor,
  AnalyticsMajor,
  SettingsMinor,
} from "@shopify/polaris-icons";
import ReportPage from "./coustmerstatics";
import { useHistory } from "react-router-dom";
import {Version} from "../../Version"

const NewPage = () => {

 

  /* UI MESS START HERE */
  const skipToContentRef = useRef(null);
  const [searchActive, setSearchActive] = useState(false);
  const [searchValue, setSearchValue] = useState("");
  const [userMenuActive, setUserMenuActive] = useState(false);
  const [mobileNavigationActive, setMobileNavigationActive] = useState(false);

  const handleSearchResultsDismiss = useCallback(() => {
    setSearchActive(false);
    setSearchValue("");
  }, []);
  const handleSearchFieldChange = useCallback((value) => {
    setSearchValue(value);
    setSearchActive(value.length > 0);
  }, []);

  const toggleUserMenuActive = useCallback(
    () => setUserMenuActive((userMenuActive) => !userMenuActive),
    []
  );
  const toggleMobileNavigationActive = useCallback(
    () =>
      setMobileNavigationActive(
        (mobileNavigationActive) => !mobileNavigationActive
      ),
    []
  );

  const history = useHistory()
  const Logout = () => {
    localStorage.removeItem("username");
    localStorage.removeItem("role");
    localStorage.removeItem("login");
    localStorage.removeItem("token");
    history.push("/login")
  };
  const userMenuActions = [
    {
      items: [{ content: "Logout", onAction: Logout }],
    },
  ];

  const userMenuMarkup = (
    <TopBar.UserMenu
      actions={userMenuActions}
      name={localStorage.getItem("username")}
      initials={localStorage.getItem("username").charAt(0)}
      open={userMenuActive}
      onToggle={toggleUserMenuActive}
    />
  );
  const searchResultsMarkup = (
    <Card>
      <ActionList
        items={[
          { content: "Shopify help center" },
          { content: "Community forums" },
        ]}
      />
    </Card>
  );

  const searchFieldMarkup = (
    <TopBar.SearchField
      onChange={handleSearchFieldChange}
      value={searchValue}
      placeholder="Search"
    />
  );

  const topBarMarkup = (
    <TopBar
      showNavigationToggle
      userMenu={userMenuMarkup}
      searchResultsVisible={searchActive}
      searchField={searchFieldMarkup}
      searchResults={searchResultsMarkup}
      onSearchResultsDismiss={handleSearchResultsDismiss}
      onNavigationToggle={toggleMobileNavigationActive}
    />
  );

  const GetRole = () => {
    return localStorage.getItem("role");
  };

  const Checkpermission = () => {
    if (GetRole() === "Admin") {
      return (
        <div>
          <Navigation.Section
            separator
            title="Metrics"
            items={[
              {
                label: "Stores Statics",
                url: "storestatics",
              },
              {
                label: "Driver Statistics",
                url: "driverstatics",
              },
              {
                label: "Customers Statistics",
                url: "/customerstatics",
              },
              {
                label: "Revenue Statistics",
                url: "/revenuestatics",
              },
            ]}
            rollup={{
              after: 2,
              view: "view",
              hide: "hide",
              activePath: "/",
            }}
            action={{
              icon: AnalyticsMajor,
              onClick: () => {},
            }}
          />
          <Navigation.Section
            separator
            title="Admin Settings"
            items={[
              {
                label: "Create Store",
                url: "createstore",
              },
              {
                label: "Store Settings",
                url: "storesetting",
              },
              {
                label: "User Settings",
                url: "usersetting"
              },
          {
            label: "General Settings",
            url: "settings",
          },
              {
                label: "Add Service",
                url: "createservice",
              },
              {
                label: "Create User",
                url: "createuser",
              },
            ]}
            rollup={{
              after: 2,
              view: "view",
              hide: "hide",
              activePath: "/",
            }}
            action={{
              icon: SettingsMinor,
              onClick: () => {},
            }}
          />
        </div>
      );
    } else {
      return <div></div>;
    }
  };
  const navigationMarkup = (
    <Navigation location="/">
      <Navigation.Section
        separator
        title="Dashboard"
        items={[
          {
            label: "Overview",
            url: "/overview",
          },
        ]}
        action={{
          icon: HomeMajor,
        }}
      />
      <Navigation.Section
        separator
        title="Order"
        items={[
          {
            label: "Order",
            url: "/order",
          },
          {
            label: "New order",
            url: "/neworder",
          },
        ]}
        action={{
          icon: OnlineStoreMajor,
          onClick: () => {},
        }}
      />
      <Navigation.Section
        separator
        title="Reports"
        items={[
          {
            label: "Reports",
            url: "/reports",
          },
        ]}
        action={{
          icon: ReportMinor,
          onClick: () => {},
        }}
      />
      <Checkpermission />
       <Banner status="info">
        <p>Current version: {Version}</p>
      </Banner>
    </Navigation>
  );
  const newPage = <ReportPage />;
  return (
    <Frame
      topBar={topBarMarkup}
      navigation={navigationMarkup}
      showMobileNavigation={mobileNavigationActive}
      onNavigationDismiss={toggleMobileNavigationActive}
      skipToContentTarget={skipToContentRef.current}
    >
      {newPage}
    </Frame>
  );
};

export default NewPage;
