import React from "react";
import { TextField, Stack, Button } from "@shopify/polaris";
import Select from "react-select";
import "./Createservice.css";

const AddOnsCheckBox = ({ inputList, setInputList }) => {
  const options = [
    { value: "KG", label: "KG" },
    { value: "per-piece", label: "per-piece" },
  ];

  // handle input change
  const handleInputChange = (e, field, index) => {
    console.log(field);
    const list = [...inputList];
    list[index][field] = e;
    setInputList(list);
    console.log(list);
  };

  // handle click event of the Remove button
  const handleRemoveClick = (index) => {
    const list = [...inputList];
    list.splice(index, 1);
    setInputList(list);
  };

  // handle click event of the Add button
  const handleAddClick = () => {
    setInputList([...inputList, { Addonsname: "", price: "", unit: "" }]);
  };

  return (
    // <Layout>
    //   <Layout.Section>
    <Stack spacing="extraTight">
      {inputList.map((x, i) => {
        return (
          <Stack>
            <TextField
              placeholder="Enter addons Name"
              name="Addonsname"
              value={x.Addonsname}
              onChange={(e) => handleInputChange(e, "Addonsname", i)}
            ></TextField>
            <TextField
              placeholder="Enter addons Price"
              name="price"
              value={x.price}
              onChange={(e) => handleInputChange(e, "price", i)}
              connectedRight={
                <Select
                  options={options}
                  value={x.unit}
                  name="unit"
                  // onChange={(data) => {
                  //   setunit(data);
                  // }}
                  onChange={(e) => handleInputChange(e, "unit", i)}
                  placeholder="Unit"
                  openOnFocus={true}
                  autofocus={true}
                  menuPortalTarget={document.body}
                  styles={{
                    menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                  }}
                  className="Unit"
                />
              }
            ></TextField>
            {inputList.length !== 1 && (
              <Button size="slim" primary onClick={() => handleRemoveClick(i)}>
                Remove
              </Button>
            )}
            {inputList.length - 1 === i && (
              <Button size="slim" primary onClick={handleAddClick}>
                Add
              </Button>
            )}
          </Stack>
        );
      })}
    </Stack>
    //   {/* </Layout.Section>
    // </Layout> */}
  );
};

export default AddOnsCheckBox;
