import React, { useState, useEffect } from "react";
import { Card, DataTable, Layout, Pagination } from "@shopify/polaris";
import axios from "axios";
import Select from "react-select";
import "./App.css";
import { API } from "../API";

export default function DataTableExample({ setChoosedStore }) {
  const [apidata, setApidata] = useState([]);
  const [Filtereddatastart, setFiltereddatastart] = useState(0);
  const [Filtereddataend, setFiltereddataend] = useState(5);
  const rows = apidata.slice(Filtereddatastart, Filtereddataend);
  const [startId, setstartId] = useState(1);
  const [endId, setendId] = useState(100);

  const gonext = () => {
    setstartId(startId + 100);
    setendId(endId + 100);
    gonextfetch(startId + 100, endId + 100, true);
  };

  const gopre = () => {
    setstartId(startId - 100);
    setendId(setendId - 100);
    gonextfetch(startId - 100, endId - 100, false);
  };

  const gonextfetch = (startid, endid, go) => {
    let RequestedURI =
      API +
      "/report" +
      "?startid=" +
      startid.toString() +
      "&endid=" +
      endid.toString();

    axios
      .get(RequestedURI, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        console.log(response.data["order"]);
        const data = response.data["order"];
        console.log(data.length);
        if (data.length === 0) {
          setstartId(startId);
          setendId(endId);
        } else {
          if (go) {
            setstartId(startId);
            setendId(endId);
          } else {
            setstartId(startId);
            setendId(endId);
          }
          setApidata(data);
        }
      })
      .catch(console.log("sorry error"));
  };

  const [StoreOptions, setStoreOptions] = useState([
    { value: "No store Found", label: "No store Found" },
  ]);
  const FetchStores = () => {
    if (localStorage.getItem("role") === "Admin") {
      axios
        .get(API + "/stores", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((response) => {
          setStoreOptions(response.data.data);
        })
        .catch((response) => {
          console.log(response);
        });
    }
  };
  const [Selectedstore, setSelectedstore] = useState("Na");
  const SelectStoreforAdmin = () => {
    if (localStorage.getItem("role") === "Admin") {
      return (
        <div>
          <Select
            name="Select Store"
            placeholder="Select Store"
            className="storeselect"
            options={StoreOptions}
            onChange={(checkselect) => {
              console.log(checkselect);
              setSelectedstore(checkselect);
              setChoosedStore(checkselect.value);
              fetchAllOrder(checkselect.value);
            }}
            openOnFocus={true}
            autofocus={true}
            value={Selectedstore}
            menuPortalTarget={document.body}
            styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
          />
        </div>
      );
    } else {
      return <div></div>;
    }
  };

  const fetchAllOrder = (Selectedstorex) => {
    console.log("fetchAllOrder order");
    let RequestApi = "";
    if (Selectedstore === "Na") {
      RequestApi =
        API +
        "/report" +
        "?startid=" +
        startId.toString() +
        "&endid=" +
        endId.toString();
    } else {
      RequestApi =
        API +
        "/report/" +
        Selectedstorex +
        "?startid=" +
        startId.toString() +
        "&endid=" +
        endId.toString();
    }
    axios
      .get(RequestApi, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        console.log(response.data["order"]);
        setApidata(response.data["order"]);
      })
      .catch(console.log("sorry error"));
  };

  useEffect(() => {
    FetchStores();
    fetchAllOrder();
  }, []);
  return (
    <div className="Reporttablepage">
      <Layout>
        <Layout.Section>
          <Card>
            <Layout.Section>
              <Layout>
                <Layout.Section>
                  <div></div>
                </Layout.Section>
                <Layout.Section secondary>
                  <SelectStoreforAdmin />
                </Layout.Section>
              </Layout>

              <DataTable
                columnContentTypes={[
                  "text",
                  "numeric",
                  "text",
                  "numeric",
                  "text",
                  "numeric",
                  "text",
                ]}
                headings={[
                  "Order ID",
                  "Customer Phone",
                  "Customer Name",
                  "Address",
                  "Pickup Date",
                  "Pickup Time",
                  "Delivery Date",
                  "Delivery Time",
                  "Service",
                  "Addons",
                  "Source",
                  "Assign Store",
                  "Order Status",
                  "Pickup by",
                  "Delivery by",
                  "Quantity",
                  "Amount (in )",
                ]}
                rows={rows}
              />
            </Layout.Section>
            <Layout.Section secondary>
              <Pagination
                hasPrevious
                onPrevious={() => {
                  if (Filtereddatastart >= 1) {
                    setFiltereddatastart(Filtereddatastart - 5);
                    setFiltereddataend(Filtereddataend - 5);
                  } else {
                    gopre();
                    setFiltereddatastart(0);
                    setFiltereddataend(5);
                  }

                  // gopre();
                }}
                hasNext
                onNext={() => {
                  if (Filtereddataend <= apidata.length) {
                    setFiltereddatastart(Filtereddatastart + 5);
                    setFiltereddataend(Filtereddataend + 5);
                  } else {
                    gonext();
                    setFiltereddatastart(0);
                    setFiltereddataend(5);
                  }
                }}
              />
            </Layout.Section>
          </Card>
        </Layout.Section>
      </Layout>
    </div>
  );
}
