import React, { useState, useEffect, useCallback } from "react";
import {
  Page,
  Card,
  DataTable,
  Banner,
  Modal,
  TextContainer,
} from "@shopify/polaris";
import { API } from "../../API";
import axios from "axios";
import Modalstaus from './ModalStatus'


const Userlist = () => {
  const [UserOption, setUserOption] = useState([]);

  const [activesucc, setActivesucc] = useState(false);
  const [triggererror, settriggererror] = useState(false);

  const handleChangesucc = useCallback(() => setActivesucc(!activesucc), [
    activesucc,
  ]);

  const handleerror = useCallback(() => settriggererror(!triggererror), [
    triggererror,
  ]);

  const Fetchalluser = () => {
    axios
      .get(API + "/users", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        console.log(response.data["data"]);
        let Data = response.data["data"].map(function (rowS) {
          let Disposabledata = rowS.map(function (item) {
            console.log(rowS[0])
            return (
              <Modalstaus
                id={rowS[0]}
                value={item}
                handleerror={handleerror}
                handleChangesucc={handleChangesucc}
              />
            );
          });
          return Disposabledata;
        });
        setUserOption(Data);
      })
      .catch(console.log("sorry error"));
  };

  useEffect(() => {
    Fetchalluser();
  }, [activesucc]);

  return (
    <Page fullWidth title="User List">
      <Card>
        <DataTable
          columnContentTypes={["text", "text", "text", "text"]}
          headings={["Username", "Role", "Location", "Status"]}
          rows={UserOption}
        />
      </Card>
      <Modal open={activesucc} onClose={handleChangesucc}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>User updated successfully</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
      <Modal open={triggererror} onClose={handleerror}>
        <Modal.Section>
          <TextContainer>
            <Banner status="critical">
              <p>Sorry user can't be updated :(</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
    </Page>
  );
};

export default Userlist;
