import React, { useCallback, useRef, useState } from "react";
import {
  Card,
  Layout,
  FormLayout,
  TextField,
  Page,
  Button,
  Stack,
  Banner,
  Modal,
  TextContainer,
} from "@shopify/polaris";
import Logox from "./img/Washh.png";
import axios from "axios";
import { useHistory } from "react-router-dom";
import { API } from "../../API";

const Login = () => {
  let history = useHistory();
  const [active, setActive] = useState(false);
  const node = useRef(null);

  const [LoginFailed, setLoginFailed] = useState(false)
  const handleclose = useCallback(() => setLoginFailed(!LoginFailed), [LoginFailed]);
  const [Text, setText] = useState("")

  const handleClick = useCallback(() => {
    node.current && node.current.input.focus();
  }, []);

  const handleFocus = useCallback(() => {
    if (node.current == null) {
      return;
    }
    node.current.input.select();
    document.execCommand("copy");
  }, []);

  const toggleModal = useCallback(() => setActive((active) => !active), []);
  const [Number, setNumber] = useState("");

  const [username, setusername] = useState("");
  const [password, setpassword] = useState("");
  const handlelogin = () => {
    axios
      .post(API + "/login", {
        username: username,
        password: password,
      })
      .then((response) => {
        if ("access_token" in response.data) {
          localStorage.setItem("login", true);
          localStorage.setItem("role", response.data.role);
          localStorage.setItem("username", response.data.username);
          localStorage.setItem("token", response.data.access_token);
          history.push("/overview");
        }
      })
      .catch((err) => {
        setText(err.response.data.detail)
        handleclose()
      });
  };
  const Logo = (
    <div>
      <img src={Logox} alt={"logo"}></img>
    </div>
  );

  return (
    <Page
      title="Login"
      breadcrumbs={[{ content: "Home", url: "/home" }]}
    >
      <Layout>
        <Layout.AnnotatedSection title="Login here" description={Logo}>
          <Card sectioned>
            <FormLayout>
              <TextField
                type="text"
                label="username"
                value={username}
                onChange={(data) => {
                  setusername(data);
                }}
              />
              <TextField
                type="password"
                label="Password"
                value={password}
                onChange={(data) => {
                  setpassword(data);
                }}
              />
              <Stack alignment="fill" distribution="equalSpacing">
                <Button primary size="slim" onClick={handlelogin}>
                  Login
                </Button>

                <Button plain size="slim" onClick={toggleModal}>
                  Forgot password
                </Button>
              </Stack>
            </FormLayout>
          </Card>
        </Layout.AnnotatedSection>
      </Layout>
      <Modal
        open={active}
        onClose={toggleModal}
        title="Get password reset Link"
      >
        <Modal.Section>
          <Stack vertical>
            <Stack.Item>
              <TextContainer>
                <p>
                  Enter your mobile Number here. We will send your a password
                  reset Link. Your IP Address has been recorded for fraud
                  prevention.
                </p>
              </TextContainer>
            </Stack.Item>
            <Stack.Item fill>
              <TextField
                ref={node}
                label="Registered Phone Number"
                onFocus={handleFocus}
                value={Number}
                placeholder="9876543210"
                prefix="+91"
                maxLength={10}
                showCharacterCount
                onChange={(data) => {
                  setNumber(data);
                }}
                connectedRight={
                  <Button primary onClick={handleClick}>
                    Send Link
                  </Button>
                }
              />
            </Stack.Item>
          </Stack>
        </Modal.Section>
      </Modal>

      <Modal open={LoginFailed} onClose={handleclose}>
            <Modal.Section>
              <Banner status="critical">
                <TextContainer><p>{Text}</p></TextContainer>
              </Banner>
            </Modal.Section>
          </Modal>
    </Page>
  );
};

export default Login;
