import React, { useState, useCallback } from "react";
import {
  Modal,
  DisplayText,
  Stack,
  Card,
  TextContainer,
  Button,
  TextField,
  Banner,
} from "@shopify/polaris";
import axios from "axios";
import "./App.css";
import Select from "react-select";
import { API } from "../API";

const PickDelShow = ({
  CurrentStatus,
  PickDate,
  PickTime,
  DropDate,
  DropSlot,
}) => {
  if (CurrentStatus === "Confirmed") {
    return (
      <Stack vertical="false">
        <DisplayText size="extraSmall">
          Current Status : {CurrentStatus}
        </DisplayText>
        <DisplayText size="extraSmall">PickDate : {PickDate}</DisplayText>
        <DisplayText size="extraSmall">PickTime : {PickTime}</DisplayText>
      </Stack>
    );
  } else if (CurrentStatus === "Confirmed_NOT_ASSING") {
    return (
      <Stack vertical="false">
        <DisplayText size="extraSmall">
          Current Status : {"Confirmed"}
        </DisplayText>
        <DisplayText size="extraSmall">PickDate : {PickDate}</DisplayText>
        <DisplayText size="extraSmall">PickTime : {PickTime}</DisplayText>
      </Stack>
    );
  } else if (CurrentStatus === "Ready_NOT_ASSING") {
    return (
      <Stack vertical="false">
        <DisplayText size="extraSmall">Current Status : {"Ready"}</DisplayText>
        <DisplayText size="extraSmall">PickDate : {PickDate}</DisplayText>
        <DisplayText size="extraSmall">PickTime : {PickTime}</DisplayText>
      </Stack>
    );
  } else {
    return (
      <Stack vertical="false">
        <DisplayText size="extraSmall">
          Current Status : {CurrentStatus}
        </DisplayText>
        <DisplayText size="extraSmall">DropDate : {DropDate}</DisplayText>
        <DisplayText size="extraSmall">DropTime : {DropSlot}</DisplayText>
      </Stack>
    );
  }
};

const DropDownx = ({ CurrentStatus, setStatus }) => {
  let DisablePicked = "yes";
  let DisableInProcess = "yes";
  let DisableReady = "yes";
  let DisableDelivered = "yes";
  if (CurrentStatus === "Confirmed") {
    DisablePicked = "no";
  } else if (CurrentStatus === "Pickup") {
    DisableInProcess = "no";
  } else if (CurrentStatus === "Inprocess") {
    DisableReady = "no";
  } else if (CurrentStatus === "Ready") {
    DisableDelivered = "no";
  } else {
    return <div></div>;
  }
  console.log(DisablePicked);
  const options = [
    { value: "Confirmed", label: "Confirmed", disabled: "yes" },
    { value: "Pickup", label: "Pickup", disabled: DisablePicked },
    { value: "Inprocess", label: "Inprocess", disabled: DisableInProcess },
    { value: "Ready", label: "Ready", disabled: DisableReady },
    { value: "Delivered", label: "Delivered", disabled: DisableDelivered },
  ];
  console.log(options);
  return (
    <Stack alignment="center">
      <DisplayText size="extraSmall">Change status :</DisplayText>
      <Select
        className="dropdownforstatus"
        options={options}
        isOptionDisabled={(option) => option.disabled === "yes"}
        onChange={(selectstatus) => {
          setStatus(selectstatus);
        }}
        menuPortalTarget={document.body}
        styles={{
          menuPortal: (base) => ({ ...base, zIndex: 9999 }),
        }}
      />
    </Stack>
  );
};

const Changequantity = ({
  CurrentStatus,
  Options,
  setboy,
  quantity,
  setquantity,
}) => {
  if (CurrentStatus === "Confirmed") {
    return (
      <Stack alignment="center">
        <DisplayText size="extraSmall">Quantity : </DisplayText>
        <TextField
          placeholder="2"
          connectedRight={<Button primary>KG</Button>}
          type="number"
          value={quantity}
          onChange={(data) => setquantity(data)}
        ></TextField>
      </Stack>
    );
  } else if (CurrentStatus === "Confirmed_NOT_ASSING") {
    return (
      <Stack alignment="center">
        <DisplayText size="extraSmall">Assing Boy : </DisplayText>
        <Select
          className="dropdownforboys"
          options={Options}
          onChange={(checkselect) => {
            setboy(checkselect);
          }}
          menuPortalTarget={document.body}
          styles={{
            menuPortal: (base) => ({ ...base, zIndex: 9999 }),
          }}
        ></Select>
      </Stack>
    );
  } else if (CurrentStatus === "Ready_NOT_ASSING") {
    return (
      <Stack alignment="center">
        <DisplayText size="extraSmall">Assing Boy : </DisplayText>
        <Select
          className="dropdownforboys"
          options={Options}
          onChange={(checkselect) => {
            setboy(checkselect);
          }}
          menuPortalTarget={document.body}
          styles={{
            menuPortal: (base) => ({ ...base, zIndex: 9999 }),
          }}
        ></Select>
      </Stack>
    );
  } else {
    return <div></div>;
  }
};

const Modalx = ({ keyx, value, setReschedule }) => {

  const [activecomplete, setActivecomplete] = useState(false);

  const handleActivebanner = useCallback(
    () => setActivecomplete(!activecomplete),
    [activecomplete]
  );

  const handlecompleteChange = () => {
    handleActivebanner();
    setActive(false);
    window.location.reload();
  };

  const [Text, setText] = useState("");
  const [Statusx, setStatusx] = useState("");
  const [quantity, setquantity] = useState("1");
  const [active, setActive] = useState(false);

  const newhandler = useCallback(() => setActive(!active), [active]);

  const handleChangeX = () => {
    if (keyx !== key) {
      fetchstate();
      fetchBoy();
      setKey(keyx);
    }
    newhandler();
  };

  const activator = <p onClick={handleChangeX}>{value}</p>;

  const [CurrentStatus, setCurrentStatus] = useState("confirmed");
  const [PickDate, setPickDate] = useState("confirmed");
  const [PickTime, setPickTime] = useState("confirmed");
  const [DropDate, setDropDate] = useState("confirmed");
  const [DropSlot, setDropSlot] = useState("confirmed");
  const [SelectBoy, setSelectBoy] = useState("");
  const fetchstate = () => {
    axios
      .get(API + "/status/" + keyx, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setCurrentStatus(response.data.status.OrderStatus);
        setPickDate(response.data.status.PickDate);
        setPickTime(response.data.status.PickTime);
        setDropDate(response.data.status.DropDate);
        setDropSlot(response.data.status.DropSlot);
      });
  };

  const [Options, setOptions] = useState([
    { value: "please Wait", label: "please wait" },
  ]);
  const fetchBoy = () => {
    axios
      .get(API + "/assing/boy", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setOptions(response.data.data);
      })
      .catch((response) => {
        console.log(response);
      });
  };

  const Assingboy = (current_status) => {
    axios
      .post(
        API + "/assing/boy",
        {
          orderid: keyx,
          assing: SelectBoy.value,
          current_status: current_status,
        },
        {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      )
      .then((response) => {
        setText(response.data.data);
        handlecompleteChange();
      })
      .catch((response) => {
        console.log(response);
      });
  };
  const [key, setKey] = useState(0);
  // useEffect(() => {
  //   if (keyx != key) {
  //     fetchstate();
  //     fetchBoy();
  //     setKey(keyx);
  //   }
  // }, []);

  const Changestatus = () => {
    let RequestedUrl = "";
    console.log(Statusx.value);
    if (Statusx.value === "Pickup") {
      RequestedUrl =
        API +
        "/orderstatus/pickup" +
        "?orderid=" +
        keyx +
        "&quantity=" +
        quantity;
    } else if (Statusx.value === "Inprocess") {
      RequestedUrl = API + "/orderstatus/Inprocess" + "?orderid=" + keyx;
    } else if (Statusx.value === "Ready") {
      RequestedUrl = API + "/orderstatus/Ready" + "?orderid=" + keyx;
    } else if (Statusx.value === "Delivered") {
      RequestedUrl = API + "/orderstatus/Delivered" + "?orderid=" + keyx;
    }
    axios
      .get(RequestedUrl, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setText(response.data.data);
        handlecompleteChange();
      })
      .catch((response) => {
        console.log(response);
      });
  };

  const Reschedule = () => {
    setReschedule(keyx);
    newhandler();
  };

  const IsDelivered = () => {
    if (CurrentStatus !== "Delivered") {
      return (
        <Button
          outline
          monochrome
          onClick={() => {
            Reschedule();
          }}
        >
          Reschedule
        </Button>
      );
    } else {
      return <div></div>;
    }
  };
  return (
    <div>
      <Modal
        activator={activator}
        open={active}
        onClose={handleChangeX}
        title={"Order ID " + keyx}
        // title={orderId}
      >
        <Modal.Section>
          <Card>
            <div className="Orderstatus">
              <Stack vertical="false">
                <PickDelShow
                  CurrentStatus={CurrentStatus}
                  PickDate={PickDate}
                  PickTime={PickTime}
                  DropDate={DropDate}
                  DropSlot={DropSlot}
                />
                <DropDownx
                  CurrentStatus={CurrentStatus}
                  setStatus={setStatusx}
                />
                <Changequantity
                  CurrentStatus={CurrentStatus}
                  Options={Options}
                  key={keyx}
                  setboy={setSelectBoy}
                  quantity={quantity}
                  setquantity={setquantity}
                />
                <Stack spacing="extraLoose" alignment="center">
                  <Stack.Item fill>
                    <Button
                      primary
                      onClick={() => {
                        if (
                          CurrentStatus === "Confirmed_NOT_ASSING" ||
                          CurrentStatus === "Ready_NOT_ASSING"
                        ) {
                          Assingboy(CurrentStatus);
                        } else {
                          Changestatus();
                        }
                      }}
                    >
                      Confirmed
                    </Button>
                  </Stack.Item>
                  <Stack.Item>
                    <IsDelivered />
                  </Stack.Item>
                </Stack>
              </Stack>
            </div>
          </Card>
        </Modal.Section>
      </Modal>
      <Modal open={activecomplete} onClose={handlecompleteChange}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>{Text}</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
    </div>
  );
};

export default Modalx;
