import React from "react";
import { Modal, Banner } from "@shopify/polaris";

const Bannerx = ({active,handleChange,Status,Msg}) => {
  return (
    <div>
      <Modal open={active} onClose={handleChange}>
        <Modal.Section>
          <Banner status={Status}>
            <p>{Msg}</p>
          </Banner>
        </Modal.Section>
      </Modal>
    </div>
  );
};

export default Bannerx