import React, { useCallback, useState, useEffect } from "react";
import {
  Page,
  Layout,
  Stack,
  Card,
  FormLayout,
  TextField,
  DisplayText,
  Button,
  Banner,
  Modal,
  TextContainer,
} from "@shopify/polaris";
import Dropdown from "react-dropdown";
import "react-dropdown/style.css";
import Select from "react-select";
import "./App.css";
import {API} from "../../API"
import axios from 'axios'

const FormTextStruc = (Label, injectcomponent) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{injectcomponent}</Layout.Section>
    </Layout>
  );
};

const FormStruc = (Label, Field) => {
  return (
    <Layout>
      <Layout.Section secondary>
        <DisplayText size="extraSmall">{Label} : </DisplayText>
      </Layout.Section>
      <Layout.Section>{Field}</Layout.Section>
    </Layout>
  );
};

const Createuser = () => {
  const [Username, setUsername] = useState("");
  const [Phone, setPhone] = useState(0);

  const options = ["PickupBoy", "StoreManager", "CoustmerCare", "Admin"];
  const defaultOption = options[0];

  const [Role, setRole] = useState("PickupBoy");

  const checkrole = () => {
    if (Role === options[0]) {
      return FormStruc("City", DropDownRegion);
    } else if (Role === options[1]) {
      return FormStruc("City", DropDownRegion);
    } else {
      return "";
    }
  };

  const checkroleforarea = () => {
    if (Role === options[0]) {
      return FormStruc("Store", DropDownArea);
    } else if (Role === options[1]) {
      return FormStruc("Store", DropDownArea);
    } else {
      return "";
    }
  };

  const DropDown = (
    <Dropdown
      options={options}
      value={defaultOption}
      onChange={(data) => {
        setRole(data["value"]);
      }}
      placeholder="Select Role"
    />
  );

  const [optionsRegion, setOptionsRegion] = useState(["Guwahati"]);
  const [SelectedRegion, setSelectedRegion] = useState({ value: "Guwahati" });
  const Getregion = () => {
    axios
      .get(API + "/regions", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        setOptionsRegion(data.data["data"]);
        console.log(data.data["data"]);
      });
  };

  const Getarea = (Region) => {
    axios
      .get(API + "/regions/area?area=" + Region.value, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((data) => {
        setArea(data.data["data"]);
      })
      .catch((data) => {
        console.log(data);
      });
  };

  useEffect(() => {
    Getregion();
  }, []);

  const DropDownRegion = (
    <Select
      options={optionsRegion}
      onChange={(data) => {
        setSelectedRegion(data);
        Getarea(data);
      }}
      placeholder="Select City"
    />
  );

  const [Area, setArea] = useState(["Please select Region First"]);
  const [SelectArea, setSelectArea] = useState({ value: "empty" });
  const DropDownArea = (
    <Select
      options={Area}
      onChange={(data) => {
        setSelectArea(data);
      }}
      openOnFocus={true}
      autofocus={true}
      placeholder="Select Store"
    />
  );

  const [value, setValue] = useState("");

  const address = (
    <TextField
      value={value}
      onChange={(data) => {
        setValue(data);
      }}
      multiline={2}
    />
  );

  const createPassword = (length) => {
    var result = "";
    var characters =
      "ABCDEFGHIJKLMNOPQRSTUVWXYZabcdefghijklmnopqrstuvwxyz0123456789";
    var charactersLength = characters.length;
    for (var i = 0; i < length; i++) {
      result += characters.charAt(Math.floor(Math.random() * charactersLength));
    }
    return result;
  };

  const [active, setActive] = useState(false);
  const [triggererror, settriggererror] = useState(false);

  const handleChange = useCallback(() => setActive(!active), [active]);

  const handleerror = useCallback(() => settriggererror(!triggererror), [
    triggererror,
  ]);
  const secretpassword = createPassword(10);
  const clean = () => {
    setUsername("");
    setPhone("");
    setValue("");
    setSelectedRegion({ value: "empty" });
    setSelectedRegion({ value: "empty" });
  };
  const CreateUserX = () => {
    if (Role === "CoustmerCare" || Role === "Admin") {
      setSelectArea({ value: "empty" });
      setSelectedRegion({ value: "empty" });
    }
    const postdata = {
      username: Username,
      phone: Phone,
      address: value,
      role: Role,
      region: SelectedRegion.value,
      store: SelectArea.value,
      password: secretpassword,
    };
    console.log(postdata);
    axios
      .post(API + "/users/create", postdata, {
        headers: {
          "content-type": "text/json",
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        console.log(response.data);
        handleChange();
        clean();
      })
      .catch((response) => {
        console.log(response);
        handleerror();
      });
  };
  return (
    <Page fullWidth>
      <Card title="Create New User">
        <div className="userform">
          <FormLayout>
            <Stack vertical="true" distribution="fill" spacing="extraTight">
              {FormTextStruc(
                "Name",
                <TextField
                  type="text"
                  placeholder="script"
                  value={Username}
                  onChange={(data) => {
                    setUsername(data);
                  }}
                />
              )}
              {FormTextStruc(
                "Phone",
                <TextField
                  type="text"
                  placeholder="9365708946"
                  value={Phone}
                  onChange={(data) => {
                    setPhone(data);
                  }}
                />
              )}
              {FormStruc("Address", address)}
              {FormStruc("Role", DropDown)}
              {checkrole()}
              {checkroleforarea()}
              <Button
                primary
                size="slim"
                onClick={() => {
                  CreateUserX();
                }}
              >
                Create User
              </Button>
            </Stack>
          </FormLayout>
        </div>
      </Card>
      <Modal open={active} onClose={handleChange}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>New User has been successfully created</p>
              <p>Your userid is : {Username}</p>
              <p>Your password is : {secretpassword}</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
      <Modal open={triggererror} onClose={handleerror}>
        <Modal.Section>
          <TextContainer>
            <Banner status="critical">
              <p>cannot create New User :( !</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
    </Page>
  );
};

export default Createuser;
