import React from "react";

import Select from "react-select";
import makeAnimated from "react-select/animated";

import { Layout, DisplayText } from "@shopify/polaris";

// TEMPORALLY COMPONENT
const animatedComponents = makeAnimated();
export default function headerselection({ setheader }) {
  const options = [
    { value: "OrderId", label: "Order ID" },
    { value: "Mobile", label: "Customer Phone" },
    { value: "Name", label: "Customer Name" },
    { value: "Address", label: "Address" },
    { value: "PickDate", label: "Pickup Date" },
    { value: "PickTime", label: "Pickup Time" },
    { value: "DropDate", label: "Delivery Date" },
    { value: "DropSlot", label: "Delivery Time" },
    { value: "Service", label: "Service" },
    { value: "AddOns", label: "Addons" },
    { value: "Source", label: "Source" },
    { value: "storeId", label: "Assign Store" },
    { value: "OrderStatus", label: "Order Status" },
    { value: "Assingpick", label: "Pickup by" },
    { value: "Assingdrop", label: "Delivery by" },
    { value: "Quantity", label: "Quantity" },
    { value: "amount", label: "Amount (in )" },
  ];

  return (
      <div >
        <Layout>
          <Layout.Section>
            <DisplayText size="extraSmall">Select Header :</DisplayText>
            <Select
              isMulti
              name="Select header"
              components={animatedComponents}
              options={options}
              onChange={(checkselect) => {
                setheader(checkselect);
              }}
              openOnFocus={true}
              autofocus={true}
              menuPortalTarget={document.body}
              styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
            />
          </Layout.Section>
        </Layout>
      </div>
  );
}
