import React, { useState, useCallback } from "react";
import { Modal } from "@shopify/polaris";
import Usersetting from "./usersetting";

const Modalx = ({ id, value,handleerror,handleChangesucc }) => {

  console.log(id)

  const [active, setActive] = useState(false);

  const handleChange = useCallback(() => setActive(!active), [active]);

  const activator = <p onClick={handleChange}>{value}</p>;

  return (
    <div>
      <Modal
        activator={activator}
        open={active}
        onClose={handleChange}
        title="Edit User"
        // title={orderId}
      >
        <Modal.Section>
          <Usersetting id={id} setActivexz={setActive} handleerror={handleerror} handleChangesucc={handleChangesucc}/>
        </Modal.Section>
      </Modal>
    </div>
  );
};

export default Modalx;
