import React from "react";
import "./App.css";
import {
  BrowserRouter as Router,
  Switch,
  Route,
  Link as RouterLink,
} from "react-router-dom";
import Overview from "./Components/Overview/OverView";
import Order from "./Components/Order/Datatable";
import NewOrder from "./Components/Neworder/NewOrderCombiner";
import Reports from "./Components/Report/ReportPage";
import Storemartics from "./Components/Matrics/storestatics/StoreReportPage";
import Drivemertics from "./Components/Matrics/driverstatics/DriverReportPage";
import Revenuemertics from "./Components/Matrics/Revenuestatics/RevenueReportPage";
import Coustmerstatics from "./Components/Matrics/coustmerstatics/coustmerReportPage";
import CreateStore from "./Components/Admin/CreateStore/createstorepage";
import Createuser from "./Components/Admin/CreateUser/createuserpage";
import Createservice from "./Components/Admin/Createservice/CreateservicePage";
import Storesetting from "./Components/Admin/StoreSetting/StoresettingPage";
import Usersetting from "./Components/Admin/Usersetting/Usersettingpage";
import Generalsetting from "./Components/Admin/Generalsetting/generalsettingpage";
import Home from "./Components/Auth/Home";
import Login from "./Components/Auth/Login/Login";
import { AppProvider } from "@shopify/polaris";
import enTranslations from "@shopify/polaris/locales/en.json";
import { PrivateRoute, LoginRoute, OnlyAdminRoute } from "./PrivateRoute";

const theme = {
  colors: {
    topBar: {
      background: "#fff",
      backgroundLighter: "#F4F6F8",
      backgroundDarker: "#DFE3E8",
      border: "#C4CDD5",
      color: "#212B36",
    },
  },
  logo: {
    width: 50,
    topBarSource: "https://washh.in//assets/images/logo.png",
    url: "https://washh.in",
    accessibilityLabel: "Washh",
  },
};

function Link({ children, url = "", ...rest }) {
  // Use an regular a tag for external and download links
  if (isOutboundLink(url) || rest.download) {
    return (
      <a href={url} {...rest}>
        {children}
      </a>
    );
  }

  return (
    <RouterLink to={url} {...rest}>
      {children}
    </RouterLink>
  );
}

function isOutboundLink(url) {
  return /^(?:[a-z][a-z\d+.-]*:|\/\/)/.test(url);
}

const App = () => {
  return (
    <AppProvider
      i18n={enTranslations}
      theme={theme}
      linkComponent={Link}
    >
      <Router>
        <Switch>
          <PrivateRoute path="/reports" component={Reports} exact />
          <PrivateRoute path="/neworder" component={NewOrder} exact />
          <PrivateRoute path="/order" component={Order} exact />
          <PrivateRoute path="/" component={Overview} exact />
          <PrivateRoute path="/overview" component={Overview} exact />
          <OnlyAdminRoute path="/storestatics" component={Storemartics} exact />
          <OnlyAdminRoute
            path="/driverstatics"
            component={Drivemertics}
            exact
          />
          <OnlyAdminRoute
            path="/customerstatics"
            component={Coustmerstatics}
            exact
          />
          <OnlyAdminRoute
            path="/Revenuestatics"
            component={Revenuemertics}
            exact
          />
          <OnlyAdminRoute path="/createstore" component={CreateStore} exact />
          <OnlyAdminRoute
            path="/createservice"
            component={Createservice}
            exact
          />
          <OnlyAdminRoute path="/storesetting" component={Storesetting} exact />
          <OnlyAdminRoute path="/usersetting" component={Usersetting} exact />
          <OnlyAdminRoute path="/createuser" component={Createuser} exact />
          <OnlyAdminRoute path="/settings" component={Generalsetting} exact />
          <Route path="/home">
            <Home />
          </Route>
          <LoginRoute path="/login" component={Login} exact />
        </Switch>
      </Router>
    </AppProvider>
  );
};

export default App;
