import React, { useCallback, useState, useEffect } from "react";
import {
  Card,
  Button,
  Modal,
  Layout,
  Page,
  TextField,
  DisplayText,
  DatePicker,
  Banner,
  FormLayout,
  ChoiceList,
  Stack,
} from "@shopify/polaris";
import "react-dropdown/style.css";
import axios from "axios";
import "react-dropdown/style.css";
import Addons from "./addons";
import moment from "moment";
import Servicelist from "./servicesList";
import "./App.css";
import { API } from "../API";
import Select from "react-select";

const NewOrderPage = () => {
  /* SOURCE RADIO BUTTON STATES START HERE */
  const [source, setSource] = useState(["unknown"]);

  const handlesourceChange = useCallback((value) => setSource(value), []);

  /* END HERE */

  /* ERROR HANDLER STATE START HERE */
  const [errorcomes, setErrorcomes] = useState(false);
  const handleerrorcomes = useCallback(() => setErrorcomes(!errorcomes), [
    errorcomes,
  ]);
  /* END HERE */

  /* SUCCESS MODEL STATE START HERE */
  const [orderadded, setOrderadded] = useState(false);
  const Handleorderadded = useCallback(() => setOrderadded(!orderadded), [
    orderadded,
  ]);
  /* END HERE */

  /* GET MONTH AND YEAR FOR DATEPICKER USING MOMENT.JS */
  const monthIndex = moment().month().toString();
  const year = moment().year().toString();
  /* END HERE */

  /* SERVICES STATE START HERE */
  const [ServicesOption, setServicesOption] = useState([
    { value: "please Wait", label: "please wait" },
  ]);
  const [services, setservices] = useState();

  const handleSelectservice = useCallback(
    (newdata) => setservices(newdata),
    []
  );

  /* END HERE *?
  
    /* DATEPICKER TRIGGING STATE START HERE  */
  const [activepick, setActivepick] = useState(false);
  const handleChangepick = useCallback(() => setActivepick(!activepick), [
    activepick,
  ]);
  const [activedrop, setActivedrop] = useState(false);
  const handleChangedrop = useCallback(() => setActivedrop(!activedrop), [
    activedrop,
  ]);
  /* END HERE */

  /* DATEPICKER PICK STATE START HERE */
  const [{ pickmonth, pickyear }, setPickDate] = useState({
    pickmonth: parseInt(monthIndex),
    pickyear: parseInt(year),
  });
  const [selectedpickDates, setSelectedpickDates] = useState({
    start: new Date(moment().toString()),
    end: new Date(moment().toString()),
  });

  const handlePickMonthChange = useCallback(
    (pickmonth, pickyear) => setPickDate({ pickmonth, pickyear }),
    []
  );

  const [pickdatevalue, setPickdatevalue] = useState(
    moment().format("MMMM Do YYYY").toString()
  );

  const [selectedpickdatetosend, setselectedpickdatetosend] = useState(
    selectedpickDates.start
  );
  const Datepickchanger = (Date) => {
    /* THIS FUNCATION WILL CHANGE THE DATE IN STATE AND CLOSE THE MODEL */
    console.log(moment(Date.start).toString());
    setSelectedpickDates(Date.start);
    setselectedpickdatetosend(moment(Date.start).add(1, "d"));
    setPickdatevalue(moment(Date.start).format("MMMM Do YYYY").toString());
    handleChangepick();
  };
  /* END HERE */

  /* DATEPICKER DROP STATE START HERE */
  const [{ dropmonth, dropyear }, setdropDate] = useState({
    dropmonth: parseInt(monthIndex),
    dropyear: parseInt(year),
  });
  const [selecteddropDates, setSelecteddropDates] = useState({
    start: new Date(moment().add(1, "d").toString()),
    end: new Date(moment().add(1, "d").toString()),
  });

  const handleDropMonthChange = useCallback(
    (dropmonth, dropyear) => setdropDate({ dropmonth, dropyear }),
    []
  );

  const [dropdatevalue, setDropdatevalue] = useState(
    moment().add(1, "d").format("MMMM Do YYYY").toString()
  );

  const [selectdropdatetosend, setselectdropdatetosend] = useState(
    selecteddropDates.start
  );
  const Datedropchanger = (Date) => {
    /* THIS FUNCATION WILL CHANGE THE DATE IN STATE AND CLOSE THE MODEL */
    console.log(Date.start);
    setSelecteddropDates(Date.start);
    setselectdropdatetosend(moment(Date.start).add(1, "d"));
    setDropdatevalue(moment(Date.start).format("MMMM Do YYYY").toString());
    handleChangedrop();
  };
  /* END HERE */

  /* STATES FOR DROPDOWN START HERE */
  const [valuenumpick, setvaluenumpick] = useState("no");
  const [valuenum2pick, setvaluenum2pick] = useState("no");
  const [valuenum3pick, setvaluenum3pick] = useState("no");
  const [valuenum4pick, setvaluenum4pick] = useState("no");
  const [valuenum5pick, setvaluenum5pick] = useState("no");
  const optionspick = [
    {
      value: "08 AM To 10 AM",
      label: "08 AM To 10 AM",
      disabled: valuenumpick,
    },
    {
      value: "10 AM To 12 PM",
      label: "10 AM To 12 PM",
      disabled: valuenum2pick,
    },
    {
      value: "01 PM To 03 PM",
      label: "01 PM To 03 PM",
      disabled: valuenum3pick,
    },
    {
      value: "03 PM To 05 PM",
      label: "03 PM To 05 PM",
      disabled: valuenum4pick,
    },
    {
      value: "05 PM To 07 PM",
      label: "05 PM To 07 PM",
      disabled: valuenum5pick,
    },
  ];

  const [valuenumdrop, setvaluenumdrop] = useState("no");
  const [valuenum2drop, setvaluenum2drop] = useState("no");
  const [valuenum3drop, setvaluenum3drop] = useState("no");
  const [valuenum4drop, setvaluenum4drop] = useState("no");
  const [valuenum5drop, setvaluenum5drop] = useState("no");
  const optionsdrop = [
    {
      value: "08 AM To 10 AM",
      label: "08 AM To 10 AM",
      disabled: valuenumdrop,
    },
    {
      value: "10 AM To 12 PM",
      label: "10 AM To 12 PM",
      disabled: valuenum2drop,
    },
    {
      value: "01 PM To 03 PM",
      label: "01 PM To 03 PM",
      disabled: valuenum3drop,
    },
    {
      value: "03 PM To 05 PM",
      label: "03 PM To 05 PM",
      disabled: valuenum4drop,
    },
    {
      value: "05 PM To 07 PM",
      label: "05 PM To 07 PM",
      disabled: valuenum5drop,
    },
  ];

  const [picktimeX, setPicktimeX] = useState(optionspick[0]);
  const [droptimeX, setDroptimeX] = useState(optionsdrop[0]);

  const [Dropx, setDropx] = useState();
  const [Pickupx, setPickupx] = useState();
  const handlepicktimedropdown = (Time) => {
    console.log(Time.value);
    setPicktimeX(Time.value);
    let Datexx = selectedpickDates.start;
    console.log(moment(Datexx));
    axios
      .post(
        API + "/timeslot",
        {
          timeslotx: Time.value,
          DateSlot: moment(Datexx),
        },
        {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      )
      .then((response) => {
        console.log(response);
        setPickupx(
          "Pickup: " + response.data.PickUp + " Drop: " + response.data.Drop
        );
      });
  };

  const handledroptimedropdown = (Time) => {
    setDroptimeX(Time.value);
    let Datexx = selecteddropDates.start;
    console.log(moment(Datexx).add(1, "d"));
    axios
      .post(
        API + "/timeslot",
        {
          timeslotx: Time.value,
          DateSlot: moment(Datexx),
        },
        {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      )
      .then((response) => {
        console.log(response);
        setDropx(
          "Pickup: " + response.data.PickUp + " Drop: " + response.data.Drop
        );
      });
  };

  /* END HERE */

  /* State For Textfield START HERE */
  const [Pincode, setPincode] = useState();
  const handlePincodeChange = useCallback(
    (newValue) => setPincode(newValue),
    []
  );
  const [Mobile, setMobile] = useState("");
  const secMobileChange = useCallback((newValue) => setMobile(newValue), []);
  const handleMobileChange = (newValue) => {
    secMobileChange(newValue);
    CheckPhoneNum(newValue);
  };
  const [Name, setName] = useState("");
  const handleNameChange = useCallback((newValue) => setName(newValue), []);
  const [Email, setEmail] = useState("");
  const handleEmailChange = useCallback((newValue) => setEmail(newValue), []);
  const [Address, setAddress] = useState("");
  const handleAddressChange = useCallback(
    (newValue) => setAddress(newValue),
    []
  );
  const [Landmark, setLandmark] = useState("");
  const handleLandmarkChange = useCallback(
    (newValue) => setLandmark(newValue),
    []
  );
  const [Locality, setLocality] = useState("");
  const handleLocalityChange = useCallback(
    (newValue) => setLocality(newValue),
    []
  );
  /* END HERE */

  const [addonsx, setaddonsx] = useState("");
  /* BUTTON CLICK HANDLER START HERE */
  const buttonClickHandler = () => {
    let addonsstring = "";
    if (addonsx !== "") {
      for (const [key, value] of addonsx.entries()) {
        console.log(key, value);
        addonsstring = addonsstring + " " + key;
      }
    }
    console.log(addonsstring);
    axios
      .post(
        API + "/order/add",
        {
          Mobile: Mobile,
          Name: Name,
          Email: Email,
          Address: Address,
          Landmark: Landmark,
          Locality: Locality,
          Pincode: Locality,
          PickDate: selectedpickdatetosend,
          PickTime: picktimeX,
          DropDate: selectdropdatetosend,
          DropSlot: droptimeX,
          Service: services.value,
          addons: addonsstring,
          Source: source[0],
          storeid: Selectedstore.value,
        },
        {
          headers: {
            "content-type": "text/json",
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        }
      )
      .then((data) => {
        let response = data;
        console.log(response);
        Handleorderadded();
      })
      .catch((err) => handleerrorcomes());
  };
  /* END HERE */

  /* CHECK IF USER DETAILS ARE ALREADY EXSIST TO AUTO FILL THE FORUM */
  const CheckPhoneNum = (Data) => {
    if (Data === "" || Data.length < 9) {
      console.log("dskjkldsfhgjk");
    } else {
      axios
        .get(API + "/checknumber?number=" + Data, {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((res) => {
          let data = res.data["Userdetails"];
          handleNameChange(data["Name"]);
          handleEmailChange(data["Email"]);
          handleAddressChange(data["Address"]);
          handleLandmarkChange(data["Landmark"]);
          handleLocalityChange(data["Locality"]);
          handlePincodeChange(data["Pincode"]);
        })
        .catch((err) => console.log(err));
    }
  };

  const [fetcheddata, setfetcheddata] = useState([]);
  const FetchAddons = (data) => {
    axios
      .get(API + "/addons/" + data, {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        setfetcheddata(response.data.data);
      })
      .catch((response) => {
        console.log(response);
      });
  };

  const isadminx = localStorage.getItem("role");
  const IsAdmin = ({ storeoption, setSelectedstore, Selectedstore }) => {
    if (isadminx === "Admin") {
      return (
        <div>
          <Layout>
            <Layout.Section secondary>
              <DisplayText size="extraSmall">Select Store : </DisplayText>
            </Layout.Section>
            <Layout.Section>
              <Select
                name="Select Store"
                placeholder="Select Store"
                options={storeoption}
                value={Selectedstore}
                onChange={(checkselect) => {
                  console.log(checkselect);
                  setSelectedstore(checkselect);
                }}
                openOnFocus={true}
                autofocus={true}
                menuPortalTarget={document.body}
                styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
              />
            </Layout.Section>
          </Layout>
        </div>
      );
    } else {
      return <div></div>;
    }
  };
  const [StoreOptions, setStoreOptions] = useState([
    { value: "No store Found", label: "No store Found" },
  ]);
  const [Selectedstore, setSelectedstore] = useState("Na");
  const FetchStores = () => {
    if (isadminx === "Admin") {
      axios
        .get(API + "/stores/active", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((response) => {
          setStoreOptions(response.data.data);
        })
        .catch((response) => {
          console.log(response);
        });
    }
  };

  const disabledManagerpick = (Newdate) => {
    let dateobject = new Date();
    let hour = dateobject.getHours();
    let newdate = new Date().getDate();
    let databasedate = new Date(Newdate).getDate();
    optionspick.forEach((data, index) => {
      let info = data["value"].toString().split(" ");
      let num = 0;
      if (info[1] === "AM") {
        num = parseInt(info[0]);
      } else if (info[1] === "PM") {
        num = parseInt(info[0]) + 12;
      }
      if (newdate === databasedate) {
        if (num <= hour || num + 1 == hour || num - 1 == hour) {
          switch (index) {
            case 0:
              console.log(0);
              setvaluenumpick("yes");
              break;
            case 1:
              console.log(1);
              setvaluenum2pick("yes");
              break;
            case 2:
              setvaluenum3pick("yes");
              break;
            case 3:
              setvaluenum4pick("yes");
              break;
            case 4:
              setvaluenum5pick("yes");
              break;
          }
        }
      } else {
        console.log("FREE EVERY TAB");
        setvaluenumpick("no");
        setvaluenum2pick("no");
        setvaluenum3pick("no");
        setvaluenum4pick("no");
        setvaluenum5pick("no");
      }
    });
  };

  const disabledManagerdrop = (Newdate) => {
    let dateobject = new Date();
    let hour = dateobject.getHours();
    let newdate = new Date(
      new Date().getTime() + 24 * 60 * 60 * 1000
    ).getDate();
    let databasedate = new Date(Newdate).getDate();
    console.log(newdate);
    console.log(databasedate);
    optionsdrop.forEach((data, index) => {
      let info = data["value"].toString().split(" ");
      let num = 0;
      if (info[1] === "AM") {
        num = parseInt(info[0]);
      } else if (info[1] === "PM") {
        num = parseInt(info[0]) + 12;
      }
      if (newdate === databasedate) {
        if (num <= hour || num + 1 == hour || num - 1 == hour) {
          console.log("#########################");
          switch (index) {
            case 0:
              console.log(0);
              setvaluenumdrop("yes");
              break;
            case 1:
              console.log(1);
              setvaluenum2drop("yes");
              break;
            case 2:
              setvaluenum3drop("yes");
              break;
            case 3:
              setvaluenum4drop("yes");
              break;
            case 4:
              setvaluenum5drop("yes");
              break;
          }
        }
      } else {
        console.log("FREE EVERY TAB");
        setvaluenumdrop("no");
        setvaluenum2drop("no");
        setvaluenum3drop("no");
        setvaluenum4drop("no");
        setvaluenum5drop("no");
      }
    });
  };

  useEffect(() => {
    FetchStores();
    disabledManagerpick(selectedpickDates.start);
    disabledManagerdrop(selecteddropDates.start);
  }, []);

  const Title = (
    <DisplayText size="small">New Order</DisplayText>
  )
  return (
    <Page fullWidth>
      <Layout>
        <Layout.Section>
          <Card title={Title}>
            <FormLayout>
              <div className="addpaddingforneworder">
                <Stack vertical="true" distribution="fill" spacing="extraTight">
                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Mobile : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Mobile}
                        onChange={handleMobileChange}
                      />
                    </Layout.Section>
                  </Layout>
                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">
                        Coustmer Name :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Name}
                        onChange={handleNameChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">
                        Coustmer Email :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="email"
                        value={Email}
                        onChange={handleEmailChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Address : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Address}
                        onChange={handleAddressChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Landmark : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Landmark}
                        onChange={handleLandmarkChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Locality : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Locality}
                        onChange={handleLocalityChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Pincode : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <TextField
                        type="text"
                        value={Pincode}
                        onChange={handlePincodeChange}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">
                        Pickup Date :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <Stack alignment="center" distribution="fill">
                        <TextField
                          type="text"
                          value={pickdatevalue}
                          onFocus={() => {
                            handleChangepick();
                          }}
                        />
                        <Modal
                          open={activepick}
                          onClose={handleChangepick}
                          title="Pick Slot : "
                        >
                          <Modal.Section>
                            <DatePicker
                              month={pickmonth}
                              year={pickyear}
                              onChange={Datepickchanger}
                              onMonthChange={handlePickMonthChange}
                              selected={selectedpickDates}
                              disableDatesBefore={
                                new Date(moment().subtract(1, "d").toString())
                              }
                            />{" "}
                          </Modal.Section>
                        </Modal>
                        <Select
                          placeholder="Select Pickup Time Slot"
                          options={optionspick}
                          isOptionDisabled={(option) => option.disabled === "yes"}
                          onChange={(checkselect) => {
                            handlepicktimedropdown(checkselect);
                          }}
                          // defaultValue={optionspick[0]}
                          openOnFocus={true}
                          autofocus={true}
                          menuPortalTarget={document.body}
                          styles={{
                            menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                          }}
                        />
                      </Stack>
                      <DisplayText size="extrasmall">{Pickupx}</DisplayText>
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extraSmall">Drop Date : </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <Stack alignment="center" distribution="fill">
                        <TextField
                          type="text"
                          value={dropdatevalue}
                          onFocus={() => {
                            handleChangedrop();
                          }}
                        />
                        <Modal
                          open={activedrop}
                          onClose={handleChangedrop}
                          title="Drop Slot : "
                        >
                          <Modal.Section>
                            <DatePicker
                              month={dropmonth}
                              year={dropyear}
                              onChange={Datedropchanger}
                              onMonthChange={handleDropMonthChange}
                              selected={selecteddropDates}
                              disableDatesBefore={new Date(moment().toString())}
                            />{" "}
                          </Modal.Section>
                        </Modal>
                        <Select
                          placeholder="Select Drop Time Slot"
                          options={optionsdrop}
                          isOptionDisabled={(option) => option.disabled === "yes"}
                          onChange={(checkselect) => {
                            handledroptimedropdown(checkselect);
                          }}
                          // defaultValue={optionsdrop[1]}
                          openOnFocus={true}
                          autofocus={true}
                          menuPortalTarget={document.body}
                          styles={{
                            menuPortal: (base) => ({ ...base, zIndex: 9999 }),
                          }}
                        />
                      </Stack>
                      <DisplayText size="extrasmall">{Dropx}</DisplayText>
                    </Layout.Section>
                  </Layout>
                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extrasmall">
                        Select services :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <Servicelist
                        Services={ServicesOption}
                        setServices={setServicesOption}
                        handleSelectservice={handleSelectservice}
                        addonsdatafetch={FetchAddons}
                      />
                    </Layout.Section>
                  </Layout>

                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extrasmall">
                        Select Addons :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <Addons
                        fetcheddata={fetcheddata}
                        setaddons={setaddonsx}
                      />
                    </Layout.Section>
                  </Layout>
                  <IsAdmin
                    storeoption={StoreOptions}
                    setSelectedstore={setSelectedstore}
                    Selectedstore={Selectedstore}
                  />
                  <Layout>
                    <Layout.Section secondary>
                      <DisplayText size="extrasmall">
                        Order Source :{" "}
                      </DisplayText>
                    </Layout.Section>
                    <Layout.Section>
                      <ChoiceList
                        choices={[
                          { label: "Whatsapp", value: "Whatsapp" },
                          { label: "Facebook", value: "Facebook" },
                          { label: "Instragram", value: "Instragram" },
                          { label: "WalkIn", value: "WalkIn" },
                        ]}
                        selected={source}
                        onChange={handlesourceChange}
                      />
                    </Layout.Section>
                  </Layout>
                </Stack>
                <Button
                  alignment="center"
                  primary
                  className="ConfirmedButton"
                  onClick={buttonClickHandler}
                >
                  Confirm Order
                </Button>
              </div>
            </FormLayout>
          </Card>
          <Modal open={orderadded} onClose={Handleorderadded}>
            <Modal.Section>
              <Banner status="success">
                <p>Order has been successfully placed</p>
              </Banner>
            </Modal.Section>
          </Modal>
          <Modal open={errorcomes} onClose={handleerrorcomes}>
            <Modal.Section>
              <Banner status="critical">
                <p>Failed There is a some kind of error</p>
              </Banner>
            </Modal.Section>
          </Modal>
        </Layout.Section>
      </Layout>
    </Page>
  );
};
export default NewOrderPage;
