import React,{useState,useEffect} from "react";
import Select from "react-select"
import axios from 'axios'
import {API} from '../API'

const SelectStore = ({fetchingfunc}) => {
  const [StoreOptions, setStoreOptions] = useState([
    { value: "No store Found", label: "No store Found" },
  ]);
  const [Selectedstore, setSelectedstore] = useState("Na");
  const FetchStores = () => {
    if (localStorage.getItem("role") === "Admin") {
      axios
        .get(API + "/stores/active", {
          headers: {
            Authorization: "Bearer " + localStorage.getItem("token"),
          },
        })
        .then((response) => {
          setStoreOptions(response.data.data);
        })
        .catch((response) => {
          console.log(response);
        });
    }
  };
  useEffect(() => {
    FetchStores();
  },[])
  if (localStorage.getItem("role") === "Admin") {
    return (
      <div>
        <Select
          name="Select Store"
          placeholder="Select Store"
          className="storeselect"
          options={StoreOptions}
          value={Selectedstore}
          onChange={(checkselect) => {
            console.log(checkselect);
            setSelectedstore(checkselect)
            fetchingfunc(checkselect.value)
          }}
          openOnFocus={true}
          autofocus={true}
          menuPortalTarget={document.body}
          styles={{ menuPortal: (base) => ({ ...base, zIndex: 9999 }) }}
        />
      </div>
    );
  } else {
    return <div></div>;
  }
};



export default SelectStore