import React, { useState, useEffect, useCallback } from "react";
import {
  Page,
  Card,
  DataTable,
  Modal,
  Banner,
  TextContainer,
} from "@shopify/polaris";
import "./App.css";
import { API } from "../../API";
import axios from "axios";
import Modalstaus from "./ModalStatus";

const StoreList = () => {
  const [StoreOption, setStoreOption] = useState([]);

  const [activesucc, setActivesucc] = useState(false);
  const [triggererror, settriggererror] = useState(false);

  const handleChangesucc = useCallback(() => setActivesucc(!activesucc), [
    activesucc,
  ]);

  const handleerror = useCallback(() => settriggererror(!triggererror), [
    triggererror,
  ]);

  const Fetchalluser = () => {
    axios
      .get(API + "/stores/json", {
        headers: {
          Authorization: "Bearer " + localStorage.getItem("token"),
        },
      })
      .then((response) => {
        console.log(response.data["data"]);
        let Data = response.data["data"].map(function (rowS) {
          let Disposabledata = rowS.map(function (item) {
            console.log(rowS[0]);
            return (
              <Modalstaus
                id={rowS[0]}
                value={item}
                handleerror={handleerror}
                handleChangesucc={handleChangesucc}
              />
            );
          });
          return Disposabledata;
        });
        setStoreOption(Data);
      })
      .catch(console.log("sorry error"));
  };

  useEffect(() => {
    Fetchalluser();
  }, [activesucc]);

  return (
    <Page fullWidth title="Store List">
      <Card>
        <DataTable
          columnContentTypes={["text", "text", "text"]}
          headings={["StoreName", "Location", "Status"]}
          rows={StoreOption}
        />
      </Card>
      <Modal open={activesucc} onClose={handleChangesucc}>
        <Modal.Section>
          <TextContainer>
            <Banner status="success">
              <p>Store updated successfully</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
      <Modal open={triggererror} onClose={handleerror}>
        <Modal.Section>
          <TextContainer>
            <Banner status="critical">
              <p>Sorry Store can't be updated :(</p>
            </Banner>
          </TextContainer>
        </Modal.Section>
      </Modal>
    </Page>
  );
};

export default StoreList;
